function changeArrowPath(x_y_ini, x_y_end) {
    const arrow_div = document.getElementById('arrow_path');
    const arrow_path = arrow_div.getElementsByTagName('path')[0];
    const original_path = arrow_path.getAttribute('d');
    const maladeta_dot = document.getElementById('maladeta_position');
    let splited = original_path.split(' ');
    let position1 = splited[1].split(',');
    let position2 = splited[2].split(',');
    if (Array.isArray(x_y_ini)) {
        position1[0] = x_y_ini[0];
        position1[1] = x_y_ini[1];
        splited[1] = position1.join(',');
    }
    if (Array.isArray(x_y_end)) {
        position2[0] = x_y_end[0];
        position2[1] = x_y_end[1];
        splited[2] = position2.join(',');
    }
    arrow_path.setAttribute('d', splited.join(' '))
}

function resetArrowPath() {
    const original_path = "M 44.632797,168.37699 130.273188,42.133955";
    const arrow_div = document.getElementById('arrow_path');
    const arrow_path = arrow_div.getElementsByTagName('path')[0];
    arrow_path.setAttribute('d', original_path)
}

function positionArrowInMaladeta(arrow, maladeta) {
    try {
        const ofsset = 7;
        const maladetaRect = maladeta.node().getBoundingClientRect();
        const arrowRect = arrow.node().getBoundingClientRect();
        const left = maladetaRect.left - arrowRect.left;
        //const top = maladetaRect.top - (arrowRect.top + arrowRect.height / 2);
        const top = maladetaRect.top - (arrowRect.top + arrowRect.height - ofsset);
        window.arrow = arrow.node();
        window.maladeta = maladeta.node();
        window.arrowRect = arrowRect;
        window.maladetaRect = maladetaRect;
        arrow.attr('transform', 'translate(' + left + ',' + top + ')');
        maladeta.style('visibility', 'hidden');
    } catch (e) {
        console.warn(e)
    }
}

function paintGlacierInMap(maladetaMap) {
    const glacier = getGlacierDrawing(maladetaMap);
    glacier.style('visibility', 'hidden');
    const spainWithArrow = maladetaMap.append('g').attr('id', 'spain_with_arrow');

    return generateSpain(spainWithArrow)
        .then(() => {
            const arrow = getArrow(spainWithArrow, '#ffffff');
            const maladeta = d3.select('#maladeta_in_spain');
            arrow.attr("id", "map_arrow");
            positionArrowInMaladeta(arrow, maladeta);
            return maladetaMap;
        })

}


export {paintGlacierInMap, getGlacierDrawing, changeArrowPath};

